body {
  font-family: "DINPro";
  line-height: 1.3;
  font-size: 16px;
  color: #3a3a3a; }

img {
  max-width: 100%;
  height: auto; }

.table-cell {
  display: table-cell;
  vertical-align: middle; }

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent; }

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0; }

.slick-list:focus {
  outline: none; }

.slick-list.dragging {
  cursor: pointer;
  cursor: hand; }

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0); }

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  margin-left: auto;
  margin-right: auto; }

.slick-track:before,
.slick-track:after {
  display: table;
  content: ''; }

.slick-track:after {
  clear: both; }

.slick-loading .slick-track {
  visibility: hidden; }

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px; }

[dir='rtl'] .slick-slide {
  float: right; }

.slick-slide.slick-loading img {
  display: none; }

.slick-slide.dragging img {
  pointer-events: none; }

.slick-initialized .slick-slide {
  display: block; }

.slick-loading .slick-slide {
  visibility: hidden; }

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent; }

.slick-arrow.slick-hidden {
  display: none; }

.slider-prev {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  text-align: center;
  line-height: 45px;
  background: #c5c6c7;
  color: #fff;
  font-size: 30px;
  cursor: pointer;
  padding-right: 3px; }
  .slider-prev--main {
    position: absolute;
    z-index: 10;
    right: 40px;
    top: 50%; }

.slider-next {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  text-align: center;
  line-height: 45px;
  background: #c5c6c7;
  color: #fff;
  font-size: 30px;
  cursor: pointer;
  top: 35%;
  padding-left: 3px; }
  .slider-next--main {
    position: absolute;
    z-index: 10;
    right: 40px; }

.slider-prev--inner {
  top: 55%; }

.slider-next--inner {
  top: 25%; }

.slick-prev {
  width: 30px;
  height: 45px;
  text-align: center;
  background: rgba(255, 255, 255, 0.4);
  color: #fff;
  cursor: pointer;
  border: 0;
  outline: 0;
  position: absolute;
  z-index: 2;
  left: 0;
  top: 50%;
  transform: translate(0, -50%);
  text-indent: -9999px;
  border-radius: 0 5px 5px 0; }
  .slick-prev:before {
    content: "";
    display: block;
    position: absolute;
    z-index: 2;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 8px;
    height: 14px;
    background: url(../images/svg/arrow_point_to_left.svg) no-repeat;
    background-size: contain; }

.slick-next {
  width: 30px;
  text-indent: -9999px;
  height: 45px;
  text-align: center;
  background: rgba(255, 255, 255, 0.4);
  color: #fff;
  cursor: pointer;
  border: 0;
  outline: 0;
  right: 0;
  top: 50%;
  position: absolute;
  transform: translate(0, -50%);
  border-radius: 5px 0 0 5px; }
  .slick-next:after {
    content: "";
    display: block;
    position: absolute;
    z-index: 2;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 8px;
    height: 14px;
    background: url(../images/svg/arrow_point_to_right.svg) no-repeat;
    background-size: contain; }

.slider-prev--carousel {
  position: absolute;
  z-index: 2;
  left: -75px;
  top: 50%;
